<template>
    <bk-dialog
        :quick-close="false"
        :has-header="true"
        :ext-cls="'common-dialog'"
        :title="i18n.title"
        width="600"
        :is-show.sync="isTaskCloneDialogShow"
        @confirm="onConfirm"
        @cancel="onCancel">
        <div slot="content">
            <div class="clone-wrapper" v-bkloading="{isLoading: pending, opacity: 1}">
                <div class="common-form-item">
                    <label>{{ i18n.template }}</label>
                    <div class="common-form-content">
                        <BaseInput
                            name="taskName"
                            v-model="name"
                            v-validate="taskNameRule">
                        </BaseInput>
                        <span v-if="errors.has('taskName')" class="common-error-tip error-msg">{{ errors.first('taskName') }}</span>
                    </div>
                </div>
            </div>
        </div>
    </bk-dialog>
</template>
<script>
import BaseInput from '@/components/common/base/BaseInput.vue'
import { NAME_REG } from '@/constants/index.js'

export default {
    name: 'TaskCloneDialog',
    components: {
        BaseInput
    },
    props: ['isTaskCloneDialogShow', 'taskName', 'pending'],
    data () {
        return {
            name: this.taskName,
            taskNameRule: {
                required: true,
                max: 50,
                regex: NAME_REG
            },
            i18n: {
                title: gettext('任务克隆'),
                template: gettext('任务名称')
            }
        }
    },
    methods: {
        onConfirm () {
            this.$validator.validateAll().then(result => {
                if (!result) {
                    return
                }
                this.$emit('confirm', this.name)
            })
        },
        onCancel () {
            this.$emit('cancel')
        }
    }
}
</script>
<style lang="scss" scoped>
    .clone-wrapper {
        padding: 30px 0;
        .common-form-item {
            label {
                font-weight: normal;
            }
        }
    }
</style>

